.spinner svg {
    width: 100%;
  }

button.mat-button,
a[button],
.dark-mode button.mat-button,
.dark-mode a[button] {
  background-color: var(--primary);
  border: 1px solid var(--primary);
  color: var(--font-light);
  min-height: 3em;
}

button.mat-button.inverse, a[button].inverse, .dark-mode button.mat-button.inverse, .dark-mode a[button].inverse {
    background-color: #fff;
    color: var(--primary);
  }

button.mat-button.success, a[button].success, .dark-mode button.mat-button.success, .dark-mode a[button].success {
    background-color: var(--success);
    border-color: var(--primary);
  }

button.mat-button.clear, a[button].clear, .dark-mode button.mat-button.clear, .dark-mode a[button].clear {
    background: none;
    border: none;
    color: var(--font-dark);
  }

button.mat-button.error, a[button].error, .dark-mode button.mat-button.error, .dark-mode a[button].error {
    background: none;
    color: var(--error);
    border-color: var(--error);
  }

button.mat-button[disabled], a[button][disabled], .dark-mode button.mat-button[disabled], .dark-mode a[button][disabled] {
    background-color: #ccc;
    border-color: rgba(#000, 0.1);
    pointer-events: none;
  }

.mat-tab-body-wrapper {
  flex-grow: 1;
}

.mat-icon-button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2em;
  width: 2em;
  font-size: 1em;
}

.mat-icon-button .mat-button-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

.mat-icon-button app-icon {
    font-size: 1.5em;
  }

.mat-icon-button[disabled] {
    opacity: .35;
  }

.panel .mat-icon-button {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 2em;
    width: 2em;
    font-size: 1em;
  }

.panel .mat-icon-button.action {
      background-color: #fff;
      border: 2px solid #000;
      box-shadow: 0 0 0 2px #fff;
      color: rgba(#000, .8);
      height: 2em;
      width: 2em;
    }

.panel .mat-icon-button .mat-button-wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
    }

.panel .mat-icon-button app-icon {
      font-size: 1em;
    }

.dropdown[widget].default {
    width: 9.5em;
  }

.dynamic-field[widget] .details {
    min-width: 100%;
  }

.dynamic-field[widget] label {
    margin-left: 0;
  }

.dynamic-field[widget] .control {
    min-width: 10em;
  }

.overlay-backdrop {
  background-color: rgba(#000, 0.6);
}

mat-slide-toggle .mat-slide-toggle-bar {
    height: 24px;
    border-radius: 12px;
  }

mat-slide-toggle .mat-slide-toggle-thumb-container {
    top: 2px;
    left: 2px;
  }

mat-slide-toggle.mat-slide-toggle.mat-checked .mat-slide-toggle-thumb-container {
    transform: translate3d(12px, 0, 0);
  }

[bg="white"] .mat-form-field-outline {
  background-color: #fff;
}

mat-dialog-container.mat-dialog-container {
  padding: 0;
}

mat-dialog-container.mat-dialog-container header {
    display: flex;
    align-items: center;
    padding: 0.5em 1em;
    width: 100%;
    background-color: var(--primary);
    color: var(--font-light);
  }

mat-dialog-container.mat-dialog-container header h1,
        mat-dialog-container.mat-dialog-container header h2,
        mat-dialog-container.mat-dialog-container header h3,
        mat-dialog-container.mat-dialog-container header h4 {
      flex: 1;
      min-width: 50%;
    }

mat-dialog-container.mat-dialog-container header > * {
      margin: 0;
    }

mat-dialog-container.mat-dialog-container .mat-dialog-content {
    margin: 0;
    padding: 0;
  }

mat-dialog-container.mat-dialog-container .mat-dialog-content .body {
      padding: 1em;
    }

mat-dialog-container.mat-dialog-container .mat-dialog-actions {
    margin-bottom: 0;
  }

mat-dialog-container.mat-dialog-container .mat-dialog-actions footer {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

mat-form-field.mat-form-field-appearance-outline.mat-focused .mat-form-field-outline-thick {
      color: var(--primary);
    }

mat-form-field.map .mat-form-field-wrapper, mat-form-field.white .mat-form-field-wrapper {
      padding: 0;
    }

mat-form-field.map .mat-form-field-outline, mat-form-field.white .mat-form-field-outline {
      background-color: #fff;
      border-radius: 4px;
    }

.mat-form-field-infix {
  border-top: none;
}

.mat-select-panel .mat-option.mat-selected:not(.mat-option-disabled) {
        color: var(--primary);
      }

mat-select .mat-select-trigger .mat-select-arrow-wrapper {
      transform: none;
    }

.mat-simple-snackbar button.mat-button {
    background: none;
    border: none;
  }

.mat-snack-bar-container.success {
    background-color: var(--success);
    color: #fff;
  }

.mat-snack-bar-container.warn {
    background-color: var(--pending);
    color: #fff;
  }

.mat-snack-bar-container.error {
    background-color: var(--error);
    color: #fff;
  }

.mat-snack-bar-container.info {
    background-color: #37474f;
    color: #fff;
  }

.mat-form-field.rounded .mat-form-field-outline-start {
      border-radius: 2em 0 0 2em;
      min-width: 2em;
    }

.mat-form-field.rounded .mat-form-field-outline-end {
      border-radius: 0 2em 2em 0;
    }

.mat-form-field.visible .mat-select-value {
      overflow: visible;
    }

.mat-form-field .mat-form-field-subscript-wrapper {
    overflow: visible;
  }

.mat-form-field .mat-form-field-wrapper .mat-form-field-label {
      top: 2em;
      margin-top: -0.1em;
    }

.mat-form-field[overlay] .mat-form-field-outline, .mat-form-field[overlay]:hover .mat-form-field-outline {
      background-color: #fff;
      border-radius: 4px;
    }

.mat-form-field .mat-form-field-should-float .mat-form-field-wrapper .mat-form-field-label {
      transform: translateY(-1.5em) scale(.75) !important;
    }

mat-slider[white].mat-slider-min-value:not(.mat-slider-thumb-label-showing) .mat-slider-thumb {
      border-color: rgba(255, 255, 255, 0.26);
    }

mat-slider[white] .mat-slider-track-background {
      background-color: rgba(255, 255, 255, 0.26);
    }

mat-slider .mat-slider-thumb {
    background-color: var(--primary);
  }

mat-slider .mat-slider-track-fill {
    background-color: var(--primary);
  }

mat-form-field.mat-form-field-appearance-outline.mat-focused .mat-form-field-outline-thick {
      color: var(--primary);
    }

[hide-outline] mat-form-field .mat-form-field-outline > * {
      border-color: rgba(0, 0, 0, 0);
    }

[hide-outline] mat-form-field:hover .mat-form-field-outline > *,
        [hide-outline] mat-form-field.mat-focused .mat-form-field-outline > * {
      border-color: inherit;
    }

[no-wrapper] mat-form-field .mat-form-field-wrapper {
      padding: 0;
      margin: 0;
    }

.mat-button-toggle-button[aria-pressed="true"] {
  background-color: var(--primary);
  color: var(--font-light);
}

.mat-chip.mat-standard-chip.mat-primary {
    color: var(--primary);
  }

.mat-chip.mat-standard-chip.mat-accent {
    background-color: var(--pending);
  }

.mat-spinner circle {
    stroke: var(--primary);
  }

.mat-progress-bar-fill::after {
    background-color: var(--primary);
  }

div.mat-menu-panel {
  max-width: calc(100vw - 1em);
}

div.mat-menu-panel .desk {
    line-height: 48px;
    padding-left: 8px;
    padding-right: 8px;
  }

div.mat-menu-panel .desk span {
      font-weight: 400;
      text-transform: capitalize;
    }

.mat-tooltip {
  font-size: 0.8em;
  background-color: rgba(#212121, 0.85);
}

.mat-table td.mat-cell:last-of-type {
    padding-right: 0;
  }

a-date-picker .date.active {
    background-color: var(--primary) !important;
  }

a-date-picker .dot {
    background-color: var(--primary) !important;
  }

.chevron {
  position: relative;
}

.chevron > div {
    position: relative;
    top: -2px;
    height: 12px;
    width: 12px;
    transform: rotate(45deg);
    transition: transform 200ms, top 200ms;
    border-bottom: 3px solid #000;
    border-right: 3px solid #000;
  }

.chevron.upside > div {
      top: 2px;
      transform: rotate(225deg);
    }

form mat-form-field,
    form a-date-field,
    form an-action-field,
    form editor {
    flex: 1;
    min-width: 50%;
  }

[disabled] .chevron {
    opacity: 0.35;
  }

.mat-menu-content .legend {
    padding-left: 8px;
    display: flex;
    align-items: center;
    line-height: 2em;
  }

.mat-menu-content .legend .color {
      border-radius: 50%;
      height: 10px;
      width: 10px;
      margin-right: 5px;
    }

#placeos .mat-datepicker-toggle button {
    position: relative;
    margin-top: -1rem;
    top: .25rem;
  }

[qr-code] {
  font-size: 6vmin;
}

[qr-code] h2 {
    font-size: 1.5em;
    margin: 0;
    padding: 0;
  }

[qr-code] span {
    font-weight: 500;
  }

[qr-code].space-y-2 > * + * {
    margin-top: 0.5em;
  }

[qr-code] img {
    height: 70vmin;
    width: 70vmin;
  }
