:root {
  --primary: #c2185b;
  --primary-dark: #ad1457;
  --secondary: #263238;
  --secondary-dark: #212121;
  --font-light: #fff;
  --font-dark: #000;
  --success: #43a047;
  --pending: #ffb300;
  --error: #e53935;
  --bg: #f0f0f0;
  --bg-dark: #212121;
  --font: 'Helvetica Neue', Roboto, Helvetica, sans-serif;
  --heading-font: Georgia, 'Times New Roman', Times, serif;
}

@media screen {
  .print-only {
    display: none !important;
  }
}

html,
body {
  background-color: #f0f0f0;
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  font-size: 16px;
  background-image: url(data:image/svg+xml;base64,);
}

* {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Helvetica Neue', Roboto, Helvetica, sans-serif;
  font-family: var(--font);
  box-sizing: border-box;
}

.material-icons {
  font-size: 1em;
}

span.highlight {
  color: #c2185b;
  color: var(--primary);
}

