.info-block {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0.5em auto;
  width: 100%;
  min-height: 6em;
}

.info-block .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 1.2em;
    width: 1.2em;
    font-size: 2em;
  }

.info-block .icon a-spinner {
      font-size: 0.25em;
    }

.info-block .text {
    margin: 0.5em;
    text-align: center;
  }

.loading-block {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background-color: #fff;
}

.loading-block .icon {
    height: 4em;
    width: 4em;
  }

.center,
[center] {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

